:root {
  --dark: #030E17;
  --border: #D4DBE7;
  --border-alt: #D9E0EE;
  --bg: #F0F2F5;
  --bg-dark: #1A1E33;
  --light: #FFFFFF;
  --blue: #405FCE;
  --error: #E14942;
  --gold: #CDA229;
  --dark-blue: #2A315C;
  --soft-blue: rgba(42, 49, 92, 0.102);
  --great-transition: all 200ms ease;
  --placeholder: rgba(42, 49, 92, 0.502);
}

* {
  box-sizing: content-box;
  outline: none;
}

html {
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
}

:root {
  font-size: 20px;
  line-height: 1.6;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
}

body {
  position: relative;
  flex-direction: column;
  display: flex;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  min-height: 100vh;
  background-color: var(--bg);
  font-weight: 500;
}

#footer {
  width: 100%;
  box-sizing: border-box;
  position: relative;
  margin-top: auto;
}

h1, h2, h3, h4, h5, h6, p {
  display: block;
  margin: 0;
  padding: 0;
  font-weight: 400;
}

.h1, .h2, .h3, .h4, .h5, .h6 {
  margin: 0;
  padding: 0;
  float: none;
}

img, input, textarea, button, a {
  border: none;
  outline: none;
}

input, textarea, button, a {
  font-family: "Montserrat", sans-serif;
  font-weight: normal;
  font-size: 1.11rem;
  line-height: 1;
}

menu {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  margin: 0;
  padding: 0;
}

td, th {
  vertical-align: top;
  word-wrap: break-word;
}

nav, dl {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  font-size: inherit;
  line-height: inherit; /*color: var(--blue);*/
  font-weight: inherit;
}

a:hover {
  text-decoration: underline;
}

button {
  display: block;
  cursor: pointer;
