  background-color: #2274F0;
  border-radius: 20px;
  overflow: hidden;
}
.picture-with-description .picture-with-description__img-box {
  position: relative;
  padding-top: 52.5%;
}
.picture-with-description .picture-with-description__img {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center center;
}
.picture-with-description .picture-with-description__desk-box {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 9px;
  padding: 9px 30px 30px 30px;
  background-color: #2274F0;
  color: var(--light);
  font-size: 16px;
  line-height: 1.5;
  font-weight: 500;
  box-sizing: border-box;
}
.picture-with-description .picture-with-description__desk-box::before {
  content: "";
  position: absolute;
  bottom: 100%;
  left: 0;
  width: 100%;
  height: 21px;
  border-radius: 20px 20px 0 0;
  background-color: #2274F0;
}
.picture-with-description .picture-with-description__title {
  font-family: "NotoSerif", sans-serif;
  font-size: 20px;
  line-height: 1.3;
  font-weight: 700;
}

